import React from 'react';
import './styles.css';
import moment from 'moment';
import { Button } from 'semantic-ui-react';

const refresh = ({weatherData}) => {
  window.location.reload();
}

const WeatherCard = ({weatherData}) => (
  <div className="main">

      <div className="top">
        <p className="header">{weatherData.name}</p>
        <Button className="button" inverted color="blue" circular icon="refresh" onClick={refresh} />
      </div>

      <div className="flex">
        <p className="day">Day: {moment().format('dddd')}</p>
        <p className="description">{weatherData.weather[0].main}</p>
      </div>

      <div className="flex">
        <p className="temp">Temperature: {weatherData.main.temp} &deg;C</p>
        <p className="temp">Humidity: {weatherData.main.humidity} %</p>
      </div>
      
      <div className="flex">
        <p className="sunrise-sunset">Sunrise: {new Date(weatherData * 1000).toLocaleTimeString('en-In')}</p>
        <p className="sunrise-sunset">Sunset: {new Date(weatherData * 1000).toLocaleTimeString('en-In')}</p>
      </div>
  </div>
)

export default WeatherCard;